@import url("./assets/styles/reset.css");
body {
  font-family: Garamond, Georgia, yu-mincho-pr6n, YuMincho, '游明朝', Yu Mincho, YuMincho, Hiragino Mincho ProN, 'HGS明朝E', serif;
}
img {
  width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
ul {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  color: #000;
}
a:hover {
  text-decoration: none;
}
.content {
  clear: both;
}
.article {
  max-width: 1310px;
  padding: 0 15px;
  margin: 80px auto;
}
.article h2,
.article h3 {
  font-weight: normal;
  margin-bottom: 80px;
}
.article h2 {
  font-size: 30px;
}
.article p {
  line-height: 2;
}
.article ul {
  margin-bottom: 80px;
}
.article li {
  margin-top: 2px;
}
.article .video-wrap {
  position: relative;
  height: 0;
  padding: 0 0 56.25%;
  overflow: hidden;
  margin-top: 2px;
}
.article .video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-width: 100%;
}
.detail {
  margin: 80px 0;
}
.detail h4 {
  font-size: 18px;
  margin-bottom: 10px;
}
.detail p {
  font-size: 15px;
  margin-bottom: 40px;
}
@media only screen and (max-width: 768px) {
  .video-wrap {
    margin-top: 50px;
  }
  .article {
    margin: 50px auto;
  }
  .article h2,
  .article h3 {
    margin-bottom: 50px;
  }
  .article li {
    margin-top: 50px;
  }
}
/*# sourceMappingURL=src/App.css.map */