header {
  height: 100px;
  padding: 0 2vw;
}
h1 {
  float: left;
  font-size: 24px;
  line-height: 100px;
}
a {
  text-decoration: none;
}
@media only screen and (max-width: 768px) {
  header {
    height: 60px;
    padding: 0 15px;
  }
  h1 {
    font-size: 20px;
    line-height: 60px;
  }
}
