
.content
  padding 0 2vw

ul
  clear both

  &.col-left-large
    li:nth-child(1)
      width 64vw
    li:nth-child(2)
      margin-top -2px
      border-top 2px solid #fff

  &.col-right-large
    li:nth-child(1)
      float right
      width 64vw
    li:nth-child(3)
      margin-top -2px
      border-top 2px solid #fff

li
  float left
  position relative
  border 1px solid #fff
  width 32vw

  div
    width 100%
    height 100%
    position absolute
    background-color #fff
    top 0
    left 0
    opacity 0
    transition opacity 0.25s

    &:hover
      opacity 0.8
    p
      line-height 1.2
      color #000
      top 2em
      left 2em
      position absolute
      span
        font-size 12px

@media only screen and (max-width: 768px)
  .content
    padding 0
  ul
    li
      width 100vw
    &.col-left-large,
    &.col-right-large
      li:nth-child(1) 
        width 100vw
    

